import React from "react";
import {
  List,
  Datagrid,
  TextField,
  SearchInput,
  Filter,
  Show,
  SimpleShowLayout,
  EditButton,
  ShowButton,
  DeleteButton,
} from "react-admin";

const UsersFilter = (props) => (
  <Filter {...props}>
    <SearchInput source="q" alwaysOn />
  </Filter>
);

export const UsersList = (props) => (
  <List
    {...props}
    filters={<UsersFilter />}
    filterDefaultValues={{ is_published: true }}
  >
    <Datagrid rowClick="show">
      <TextField source="userId" />
      <TextField source="firstName" />
      <TextField source="lastName" />
      <TextField source="userName" />
      <ShowButton />
      <EditButton />
      <DeleteButton />
    </Datagrid>
  </List>
);

export const UsersShow = (props) => (
  <Show {...props}>
    <SimpleShowLayout>
      <TextField source="userId" />
      <TextField source="firstName" />
      <TextField source="lastName" />
      <TextField source="username" />
    </SimpleShowLayout>
  </Show>
);
