import React from "react";
import {
  List, Datagrid, TextField, SearchInput, TextInput, Filter, Create, SimpleForm, Edit, 
  DateField, DateInput, Show, SimpleShowLayout, EditButton, TopToolbar, Button, ShowButton,
  NumberInput, DeleteButton
} from "react-admin";

const AvisFilter = (props) => (
  <Filter {...props}>
    <SearchInput source="q" alwaysOn />
  </Filter>
);

export const AvisList = (props) => (
  <List
    {...props}
    filters={<AvisFilter />}
    filterDefaultValues={{ is_published: true }}
  >
    <Datagrid rowClick="show">
      <TextField source="id" />
      <TextField source="note" />
      <DateField source="creationDate" />
      <ShowButton />
      <EditButton />
      <DeleteButton />
    </Datagrid>
  </List>
);

export const AvisCreate = (props) => (
      <Create {...props}>
        <SimpleForm>
          <NumberInput source="note" />
          <DateInput source="creationDate" />
        </SimpleForm>
      </Create>
);

const AvisEditActions = ({ basePath, data, resource }) => (
  <TopToolbar>
    <ShowButton basePath={basePath} record={data} />
  </TopToolbar>
);


export const AvisEdit = (props) => (
  <Edit actions={<AvisEditActions />} {...props}>
    <SimpleForm>
      <TextInput source="note" />
      <DateInput source="creationDate" />
    </SimpleForm>
  </Edit>
);

const AvisShowActions = ({ basePath, data, resource }) => (
  <TopToolbar>
    <EditButton basePath={basePath} record={data} />
    <Button color="primary">
      Custom Action
    </Button>
  </TopToolbar>
);

export const AvisShow = (props) => (
  <Show {...props}>
    <SimpleShowLayout>
      <TextField source="id" />
      <TextField source="note" />
      <DateField source="creationDate" />
    </SimpleShowLayout>
  </Show>
);