import React from "react";
import {
  List, Datagrid, TextField, SearchInput, TextInput, Filter, Create, SimpleForm, Edit, 
  Show, SimpleShowLayout, EditButton, TopToolbar, ShowButton, NumberInput, DeleteButton,
  DateField, DateInput,
} from "react-admin";

const MessagesFilter = (props) => (
  <Filter {...props}>
    <SearchInput source="q" alwaysOn />
  </Filter>
);

export const MessagesList = (props) => (
  <List
    {...props}
    filters={<MessagesFilter />}
    filterDefaultValues={{ is_published: true }}
  >
    <Datagrid rowClick="show">
          <TextField source="id" />
          <TextField source="subject" />
          <DateField source="creationDate" />
          <ShowButton />
          <EditButton />
          <DeleteButton />
    </Datagrid>
  </List>
);

export const MessagesCreate = (props) => (
         <Create {...props}>
           <SimpleForm>
             <TextInput source="subject" />
             <TextInput multiline source="body" />
           </SimpleForm>
         </Create>
       );

const MessagesEditActions = ({ basePath, data, resource }) => (
  <TopToolbar>
    <ShowButton basePath={basePath} record={data} />
  </TopToolbar>
);

export const MessagesEdit = (props) => (
  <Edit actions={<MessagesEditActions />} {...props}>
    <SimpleForm>
        <TextInput source="subject" />
        <TextInput multiline source="body" />
    </SimpleForm>
  </Edit>
);

export const MessagesShow = (props) => (
  <Show {...props}>
    <SimpleShowLayout>
          <TextField source="id" />
          <TextField source="subject" />
          <TextField source="body" />
          <DateField source="creationDate" />
    </SimpleShowLayout>
  </Show>
);