import React from "react";
import {
  List, Datagrid, TextField, SearchInput, TextInput, Filter, Create, SimpleForm, Edit, 
  Show, SimpleShowLayout, EditButton, TopToolbar, ShowButton, NumberInput, DeleteButton,
} from "react-admin";

const ProduitFilter = (props) => (
  <Filter {...props}>
    <SearchInput source="q" alwaysOn />
  </Filter>
);

export const ProduitList = (props) => (
  <List
    {...props}
    filters={<ProduitFilter />}
    filterDefaultValues={{ is_published: true }}
  >
    <Datagrid rowClick="show">
          <TextField source="id" />
          <TextField source="name" />
          <TextField source="prix" />
          <ShowButton />
          <EditButton />
          <DeleteButton />
    </Datagrid>
  </List>
);

export const ProduitCreate = (props) => (
         <Create {...props}>
           <SimpleForm>
             <TextInput source="name" />
             <NumberInput source="prix" />
             <TextInput multiline source="description" />
           </SimpleForm>
         </Create>
       );

const ProduitEditActions = ({ basePath, data, resource }) => (
  <TopToolbar>
    <ShowButton basePath={basePath} record={data} />
  </TopToolbar>
);

export const ProduitEdit = (props) => (
  <Edit actions={<ProduitEditActions />} {...props}>
    <SimpleForm>
        <TextInput source="name" />
        <NumberInput source="prix" />
    </SimpleForm>
  </Edit>
);

export const ProduitShow = (props) => (
  <Show {...props}>
    <SimpleShowLayout>
          <TextField source="id" />
          <TextField source="name" />
          <TextField source="prix" />
          <TextField source="description" />
    </SimpleShowLayout>
  </Show>
);