import * as React from "react";
import { fetchUtils, Admin, Resource } from "react-admin";
import simpleRestProvider from 'ra-data-simple-rest';
import authProvider from './authProvider';
import { AvisList, AvisCreate, AvisShow, AvisEdit } from "./Components/Avis";
import { ProduitList, ProduitCreate, ProduitShow, ProduitEdit } from "./Components/Produit";
import { MessagesList, MessagesCreate, MessagesShow, MessagesEdit } from "./Components/Messages";
import { UsersList, UsersShow } from "./Components/Users";
import frenchMessages from "ra-language-french";
import polyglotI18nProvider from "ra-i18n-polyglot";
import PersonIcon from "@material-ui/icons/Person";
import MessageIcon from "@material-ui/icons/Message";
import FeedbackIcon from "@material-ui/icons/Feedback";
import LocalMallIcon from "@material-ui/icons/LocalMall";
import { createMuiTheme } from '@material-ui/core/styles';
import pink from '@material-ui/core/colors/pink';
import blue from '@material-ui/core/colors/blue';

const i18nProvider = polyglotI18nProvider(() => frenchMessages, "fr");

const fetchJson = (url, options = {}) => {
  if (!options.headers) {
    options.headers = new Headers({ Accept: "application/json" });
  }
  const token = localStorage.getItem("token");
  options.headers.set("Authorization", `Bearer  ${token}`);
  return fetchUtils.fetchJson(url, options);
};
const dataProvider = simpleRestProvider("http://localhost:56407/api", fetchJson);

const theme = createMuiTheme({
  palette: {
    primary: blue,
    secondary: pink,
  }, // Switching the dark mode on is a single property value change.
});

const permissions = localStorage.getItem("permissions");

const App = () => (
  <Admin title="Clic'n Fest Admin" locale="fr" i18nProvider={i18nProvider} dataProvider={dataProvider} 
     authProvider={authProvider} theme={theme}>
    {permissions => [
      <Resource name="Produit" icon={LocalMallIcon} list={ProduitList} create={ProduitCreate} show={ProduitShow} edit={ProduitEdit} />,
      <Resource name="Messages" icon={MessageIcon} list={MessagesList} create={MessagesCreate} show={MessagesShow} edit={MessagesEdit} />,
      <Resource name="Avis" icon={FeedbackIcon} list={AvisList} create={AvisCreate} show={AvisShow} edit={AvisEdit} />,
        permissions === 'admin'
        ? <Resource name="Users" icon={PersonIcon} list={UsersList} show={UsersShow} />
        : null,
    ]}
  </Admin>
);
export default App;
